.project-image {
    width: 100%;
    border-radius: 2px;
    margin-bottom: 10px;
  }
  
  /* .project-details {
    text-align: left;
    margin-left: 10px; /* Adjust this value as needed */
    /* display: flex; */
    /* flex-direction: column; */
    /* justify-content: flex-start; */
  /* } */ 
  
  .detail-bg {
    padding-top: 20px;
    padding-bottom: 20px;
  }
  
  .project-container {
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
  }
  

  .detail-bg {
    text-align: left;
    background-color: #a0d6ef;
    min-height: 30vw;
    display: flex;
    /* Dark background color */
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
    text-align: left;
    padding: 20px; 
    border-bottom: 2px solid #6497fe;  

    /* font-size: calc(10px + 2vmin) */
  } 

  @media (max-width: 600px) {
    .project-image {
      width: 100%; /* Ensure the image takes full width on smaller screens */
    }
    .project-container {
      flex-direction: column;
    }
  }
  