/* src/Header.css */

.Header {
    text-align: center;
   
    /* height: 40vw; */
  }
  
  .App-headers {
    background-color: #282c34;
    display: flex; /* Dark background color */
    justify-content: center;
    align-items: center;
    flex-direction: column;
    color: white; /* White text color */
    padding: 20px; /* Padding around the header */
    border-bottom: 2px solid #61dafb; /* Bottom border for a visual separation */
    height: 20vw;
    font-size: calc(10px + 2vmin);
    
  }
  
  .App-header h1 {
    font-size: 2.5rem; /* Larger font size for the title */
    margin: 0; /* Remove default margin */
  }
  
  .App-header p {
    font-size: 1.2rem; /* Slightly larger font size for the paragraph */
    margin: 10px 0 0; /* Add margin at the top, remove default margin at the bottom */
  }
