/* src/App.css */

.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  padding: 20px;
  color: white;
}

.projects-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding: 20px;
}

.project {
  border: 1px solid #ddd;
  border-radius: 8px;
  margin: 10px;
  padding: 10px;
  width: 200px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.project img {
  width: 100%;
  border-radius: 4px;
}

.project h3 {
  font-size: 1.2em;
  margin: 10px 0;
}

.project p {
  font-size: 0.9em;
  color: #333;
}

.project a {
  display: block;
  margin-top: 10px;
  color: #61dafb;
  text-decoration: none;
}

.project a:hover {
  text-decoration: underline;
}


@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
